    };
  },
  head() {
    return {
      title: "Contact GlennPacker.net",
      meta: [
        {
          hid: "description",
          name: "description",
          content: "contact glennpacker.net"
        }
      ]
    };
  },
  methods: {
    handleSubmit() {
      this.$validator.validateAll().then(success => {
        if (success) {
          this.sendContact();
        }

        return this;
      });
    },
    async sendContact() {
      try {
        await this.$axios.$post(
          "http://glennpacker.net/api/contact",
          this.contact
        );
